import React, {useEffect, useState, useRef} from 'react'
import axios, {cacheAdapterEnhancer} from 'axios'
import Speech from 'react-speech';
import styled from 'styled-components'
import useInterval from '../hook/useInterval'
import {BsFillPlayFill, BsFillStopFill} from 'react-icons/bs'

const theme = 'darkgray'
const borderColor = '#2a2a2a'

const Container = styled.div`    
    // border: 1px solid ${borderColor};
    // padding: 15px 10px;
    // border-radius: 10px;
    color: ${theme};    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    & > * {
        margin-bottom: 10px;
    }    
    & > *:last-child {
        margin: 0;
    }

    //가격
    & > *:nth-child(2){
        font-size: 25px;
        line-height: 40px;
    }
`

const StyledSelect = styled.select`
    background-color: black;  
    color: ${theme};  
    font-size: 13px;
    border: 0;
    // padding: 8px;
`
const Button = styled.div`
    // width: 100px;
    // font-size: 14px;
    // min-height: 30px;
    // line-height: 30px;
    // border-radius: 20px;
    // border: 1px solid ${borderColor};    
    cursor: pointer;        
`

const SpeechWrap = styled.div`
    visibility: hidden
`;


const DELAY = 10000 //10초

function CoinPrice() {

    const [coin, setCoin] = useState('BLY')
    const [data, setData] = useState()
    const [delay, setDelay] = useState(DELAY)
    const currencyRef = useRef()

    useEffect(() => {    
        search(coin)
    }, [])

    useInterval(() => {        
        search(coin)     
    }, delay)
  
    const search = async (_coin) => {

      const res = await axios.get(`https://api.bithumb.com/public/ticker/${_coin}_KRW` + '_' + new Date().getTime());
              
        const newData = res.data.data
        
        setData(newData)

        // if(data){        
        //     if(newData.closing_price !== data.closing_price) {
        //         document.getElementsByTagName('button')[0].click()       
        //     }
        // }
    }

    useEffect(() => {
        console.log('===='+currencyRef.current)
        if(data) {
            console.log(currencyRef.current)
            if(data.closing_price !== currencyRef.current) {
                currencyRef.current = data.closing_price
                document.getElementsByTagName('button')[0].click()       
            }
        }
    },[data])

    const toggle = () => {        
        const newDelay = delay ? null : DELAY
        
        //시작 하면 바로 한번 조회
        if(newDelay) {
            search(coin)
        }
        
        //인터벌 시작 or 종료
        setDelay(newDelay)              
        
    }

    const onSelectChange = (e) => {
        setCoin(e.target.value)    
        
        search(e.target.value)
    }

    if(!data) return null
  
    return (
        <div style={{
            flexDirection: 'column',            
            height: '100vh', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>


            <Container>
                <StyledSelect onChange={onSelectChange}>
                    <option value="BLY">BLY 블로서리</option>
                    <option value="BTC">BTC 비트코인</option>
                    <option value="XRP">XRP 리플</option>
                    <option value="EOS">EOS 이오스</option>
                </StyledSelect>
                <div>{parseFloat(data.closing_price).toLocaleString('ko-KR')}원</div>

                
                {/* {delay ? 'stop' : 'play'} */}
                <Button onClick={toggle}>
                {
                    delay ? <BsFillStopFill size={30} /> : <BsFillPlayFill size={30}/>
                }
                </Button>                                
            </Container>

            <div style={{marginTop: 16, color: theme, textAlign:'center', fontSize: 12}}>존버는 승리한다 - made by Jaden</div>

            <SpeechWrap >
                <Speech        
                    displayText={false}
                    stop={true}
                    pause={true}
                    resume={true}
                    // textAsButton={true}
                    // voice='Daniel'
                    // lang={'en'}
                    text={parseFloat(data.closing_price).toLocaleString('ko-KR')}     
                    lang={'ko-KR'}                    
                />
            </SpeechWrap>
        </div>
    );
}
  
export default CoinPrice